// src/pages/community/write.tsx
import React, { useState, useEffect } from 'react';
import { useRouter } from 'next/router';
import Layout from '@/component/layout/MainLayout';
import SimpleTiptapEditor from '@/component/communityService/SimpleTiptapEditor';
import styles from '@/styles/community/CommunityWrite.module.css';
import {
  createConcern,
  createProject,
  createStudy
} from '@/api/communityService/community';
import {
  CreateConcernRequest,
  CreateProjectRequest,
  CreateStudyRequest,
  CommunityPostType,
  RecruitmentStatus,
  PostFormat
} from '@/types/communityService/communityType';

export default function CommunityWrite() {
  const router = useRouter();
  const [userSignId, setUserSignId] = useState<string>('');
  const [postType, setPostType] = useState<CommunityPostType>(CommunityPostType.CONCERN);
  const [title, setTitle] = useState('');
  const [content, setContent] = useState('');
  const [tags, setTags] = useState<string[]>([]);
  const [tagInput, setTagInput] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);

  const [format, setFormat] = useState<PostFormat | ''>('');
  const [deadline, setDeadline] = useState<string>('');
  const [teamSize, setTeamSize] = useState<number | ''>(''); // number | '' ÏÉÅÌÉúÎ°ú Ïú†ÏßÄ
  const [recruitmentStatus, setRecruitmentStatus] = useState<RecruitmentStatus>('RECRUITING' as RecruitmentStatus);

  // STUDY Ï†ÑÏö© ÌïÑÎìú
  const [startDate, setStartDate] = useState<string>('');
  const [schedule, setSchedule] = useState<string>('');

  useEffect(() => {
    const userId = localStorage.getItem('userSignId');
    if (!userId) {
      alert('Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§.');
      router.push('/login');
      return;
    }
    setUserSignId(userId);
  }, [router]); // routerÎ•º ÏùòÏ°¥ÏÑ± Î∞∞Ïó¥Ïóê Ï∂îÍ∞Ä

  const handleTagAdd = () => {
    const trimmedTag = tagInput.trim();

    if (trimmedTag.length < 2 || tags.includes(trimmedTag) || tags.length >= 5) {
      setTagInput('');
      return;
    }

    setTags([...tags, trimmedTag]);
    setTagInput(''); // ÌÉúÍ∑∏ Ï∂îÍ∞Ä ÌõÑ ÏûÖÎ†• ÏÉÅÌÉúÎ•º ÌôïÏã§Ìûà ÎπÑÏõÅÎãàÎã§.
  };

  const handleTagRemove = (tagToRemove: string) => {
    setTags(tags.filter(tag => tag !== tagToRemove));
  };

  const handleTagInputKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !(e.nativeEvent as any).isComposing) {
      e.preventDefault();
      e.stopPropagation();
      handleTagAdd();
    }
  };
  const handleSubmit = async () => {
    if (!title.trim()) {
      alert('Ï†úÎ™©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
      return;
    }
    if (!content.trim() || content === '<p></p>') {
      alert('ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
      return;
    }

    // ‚ú® [ÏàòÏ†ï] ÌÉÄÏûÖÎ≥Ñ Ïú†Ìö®ÏÑ± Í≤ÄÏÇ¨ Î°úÏßÅ
    if (postType === CommunityPostType.PROJECT) {
      if (!format) {
        alert('ÏßÑÌñâ Î∞©ÏãùÏùÄ ÌïÑÏàòÏûÖÎãàÎã§.');
        return;
      }
      if (!deadline) {
        alert('ÎßàÍ∞êÏùºÏùÄ ÌïÑÏàòÏûÖÎãàÎã§.');
        return;
      }

      // ‚úÖ [Ï∂îÍ∞Ä] ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ Ï∏° ÎÇ†Ïßú Ïú†Ìö®ÏÑ± Í≤ÄÏÇ¨: ÎßàÍ∞êÏùºÏù¥ Ïò§Îäò Ïù¥ÌõÑÏù∏ÏßÄ ÌôïÏù∏ (ÏãúÍ∞Ñ Î¨¥Ïãú)
      const today = new Date();
      // ÏãúÍ∞Ñ Ï†ïÎ≥¥Î•º 00:00:00ÏúºÎ°ú ÏÑ§Ï†ïÌïòÏó¨ ÎÇ†ÏßúÎßå ÎπÑÍµê Í∞ÄÎä•ÌïòÍ≤å Ï°∞Ï†ï
      today.setHours(0, 0, 0, 0);

      const deadlineDate = new Date(deadline);
      deadlineDate.setHours(0, 0, 0, 0); // ÌòπÏãú Î™®Î•º Î∏åÎùºÏö∞Ï†Ä Ìï¥ÏÑù Ï∞®Ïù¥ Î∞©ÏßÄ

      if (deadlineDate.getTime() < today.getTime()) {
        alert('ÎßàÍ∞êÏùºÏùÄ Ïò§Îäò ÎòêÎäî Ïò§Îäò Ïù¥ÌõÑÏó¨Ïïº Ìï©ÎãàÎã§.');
        return;
      }
    }

    if (postType === CommunityPostType.STUDY) {
      if (!format) {
        alert('ÏßÑÌñâ Î∞©ÏãùÏùÄ ÌïÑÏàòÏûÖÎãàÎã§.');
        return;
      }
      if (!startDate) {
        alert('ÏãúÏûëÏùºÏùÄ ÌïÑÏàòÏûÖÎãàÎã§.');
        return;
      }
    }
    setIsSubmitting(true);
    try {
      let communityId: number;

      switch (postType) {
        case CommunityPostType.CONCERN: {
          const request: CreateConcernRequest = {title, content, tagNames: tags};
          communityId = await createConcern(userSignId, request);
          break;
        }
        case CommunityPostType.PROJECT: {
          // ‚ú® [ÏàòÏ†ï] teamSize Ï≤òÎ¶¨ Î°úÏßÅ (number | '' -> number | undefined)
          const finalTeamSize: number | undefined = teamSize === '' ? undefined : (teamSize as number);
          let formattedDeadline = '';
          if (deadline) {
            formattedDeadline = `${deadline}T23:59:59`; // YYYY-MM-DDT00:00:00 ÌòïÏãùÏúºÎ°ú Î≥ÄÌôò
          } else {
            // Ïù¥ Î∂ÑÍ∏∞Îäî ÏÉÅÏúÑ Ïú†Ìö®ÏÑ± Í≤ÄÏÇ¨ÏóêÏÑú Í±∏Îü¨Ï°åÏñ¥Ïïº Ìï©ÎãàÎã§.
            alert('ÎßàÍ∞êÏùº Îç∞Ïù¥ÌÑ∞ Ï†ÑÏÜ° Ïò§Î•ò (ÎÇ¥Î∂Ä)');
            setIsSubmitting(false);
            return;
          }
          const baseRequest: CreateProjectRequest = {
            title,
            content,
            tagNames: tags,
            format: format as PostFormat,
            deadline : formattedDeadline,
            status: recruitmentStatus,
          };

          const request: CreateProjectRequest = {
            ...baseRequest,
            // finalTeamSizeÍ∞Ä numberÏùº ÎïåÎßå teamSize ÌïÑÎìúÎ•º Ï∂îÍ∞ÄÌïòÏó¨ ÌÉÄÏûÖ ÏóêÎü¨Î•º Ìï¥Í≤∞Ìï©ÎãàÎã§.
            ...(finalTeamSize !== undefined && { teamSize: finalTeamSize }),
          };

          communityId = await createProject(userSignId, request);
          break;
        }
        case CommunityPostType.STUDY: {

          const request: CreateStudyRequest = {
            title,
            content,
            status: recruitmentStatus,
            tagNames: tags,
            format: format as PostFormat,
            startDate,
            schedule: schedule || undefined, // scheduleÏù¥ Îπà Î¨∏ÏûêÏó¥Ïù¥Î©¥ undefinedÎ°ú Ï≤òÎ¶¨
          };
          communityId = await createStudy(userSignId, request);
          break;
        }
        default:
          throw new Error('Invalid post type');
      }
      alert('Í≤åÏãúÍ∏ÄÏù¥ ÏûëÏÑ±ÎêòÏóàÏäµÎãàÎã§.');
      router.push(`/community/${postType.toLowerCase()}/${communityId}`);
    } catch (error) {
      console.error('Failed to create post:', error);
      alert('Í≤åÏãúÍ∏Ä ÏûëÏÑ±Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
      <Layout>
        <div className={styles.container}>
          <div className={styles.header}>
            <h1 className={styles.title}>Í∏ÄÏì∞Í∏∞</h1>
            <button
                onClick={() => router.back()}
                className={styles.backButton}
            >
              Ï∑®ÏÜå
            </button>
          </div>

          <div className={styles.form}>
            <div className={styles.typeSelector}>
              <label className={styles.label}>Ïπ¥ÌÖåÍ≥†Î¶¨</label>
              <div className={styles.typeButtons}>
                <button
                    type="button"
                    onClick={() => setPostType(CommunityPostType.CONCERN)}
                    className={`${styles.typeButton} ${postType === CommunityPostType.CONCERN ? styles.typeButtonActive : ''} ${styles.typeConcern}`}
                >
                  Í≥†ÎØºÏûàÏñ¥Ïöî
                </button>
                <button
                    type="button"
                    onClick={() => setPostType(CommunityPostType.PROJECT)}
                    className={`${styles.typeButton} ${postType === CommunityPostType.PROJECT ? styles.typeButtonActive : ''} ${styles.typeProject}`}
                >
                  ÌîÑÎ°úÏ†ùÌä∏
                </button>
                <button
                    type="button"
                    onClick={() => setPostType(CommunityPostType.STUDY)}
                    className={`${styles.typeButton} ${postType === CommunityPostType.STUDY ? styles.typeButtonActive : ''} ${styles.typeStudy}`}
                >
                  Ïä§ÌÑ∞Îîî
                </button>
              </div>
            </div>

            <div className={styles.inputGroup}>
              <label className={styles.label}>Ï†úÎ™©</label>
              <input
                  type="text"
                  value={title}
                  onChange={(e) => setTitle(e.target.value)}
                  placeholder="Ï†úÎ™©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
                  className={styles.input}
                  maxLength={100}
              />
              <span className={styles.charCount}>{title.length}/100</span>
            </div>

            <div className={styles.inputGroup}>
              <label className={styles.label}>ÎÇ¥Ïö©</label>
              <SimpleTiptapEditor content={content} onChange={setContent}/>
            </div>

            <div className={styles.inputGroup}>
              <label className={styles.label}>ÌÉúÍ∑∏ (ÏµúÎåÄ 5Í∞ú)</label>
              <div className={styles.tagInputWrapper}>
                <input
                    type="text"
                    value={tagInput}
                    onChange={(e) => setTagInput(e.target.value)}
                    onKeyDown={handleTagInputKeyDown}
                    placeholder="ÌÉúÍ∑∏Î•º ÏûÖÎ†•ÌïòÍ≥† EnterÎ•º ÎàÑÎ•¥ÏÑ∏Ïöî"
                    className={styles.input}
                    maxLength={20}
                    disabled={tags.length >= 5}
                />
                <button
                    type="button"
                    onClick={handleTagAdd}
                    className={styles.tagAddButton}
                    disabled={tags.length >= 5}
                >
                  Ï∂îÍ∞Ä
                </button>
              </div>
              {tags.length > 0 && (
                  <div className={styles.tagList}>
                    {tags.map(tag => (
                        <div key={tag} className={styles.tag}>
                          <span>#{tag}</span>
                          <button
                              type="button"
                              onClick={() => handleTagRemove(tag)}
                              className={styles.tagRemoveButton}
                          >
                            √ó
                          </button>
                        </div>
                    ))}
                  </div>
              )}
            </div>

            {/* ‚ú® PROJECT/STUDY Í≥µÌÜµ ÌïÑÎìú */}
            {(postType === CommunityPostType.PROJECT || postType === CommunityPostType.STUDY) && (
                <>
                  <div className={styles.inputGroup}>
                    <label className={styles.label}>ÏßÑÌñâ Î∞©Ïãù (ÌïÑÏàò)</label>
                    <select
                        value={format}
                        onChange={(e) => setFormat(e.target.value as PostFormat | '')}
                        className={styles.input}
                    >
                      <option value="">ÏÑ†ÌÉù</option>
                      <option value="ONLINE">Ïò®ÎùºÏù∏</option>
                      <option value="OFFLINE">Ïò§ÌîÑÎùºÏù∏</option>
                      <option value="HYBRID">Ïò®/Ïò§ÌîÑÎùºÏù∏ Î≥ëÌñâ</option>
                    </select>
                  </div>

                {/*  <div className={styles.inputGroup}>
                    <label className={styles.label}>Î™®Ïßë ÏÉÅÌÉú (ÌïÑÏàò)</label>
                    <select
                        value={recruitmentStatus}
                        onChange={(e) => setRecruitmentStatus(e.target.value as RecruitmentStatus)}
                        className={styles.input}
                    >
                      <option value="RECRUITING">Î™®ÏßëÏ§ë</option>
                      <option value="COMPLETED">Î™®Ïßë ÎßàÍ∞ê</option>
                    </select>
                  </div>*/}
                </>
            )}

            {/* PROJECT Ï†ÑÏö© ÌïÑÎìú */}
            {postType === CommunityPostType.PROJECT && (
                <>
                  <div className={styles.inputGroup}>
                    <label className={styles.label}>Î™®Ïßë ÎßàÍ∞êÏùº</label>
                    <input
                        type="date"
                        value={deadline}
                        onChange={(e) => setDeadline(e.target.value)}
                        className={styles.input}
                    />
                  </div>

                  <div className={styles.inputGroup}>
                    <label className={styles.label}>Î™®Ïßë Ïù∏Ïõê (ÏÑ†ÌÉù)</label>
                    <input
                        type="number"
                        value={teamSize}
                        onChange={(e) => setTeamSize(Number(e.target.value) || '')}
                        placeholder="Ïà´ÏûêÎßå ÏûÖÎ†• (Ïòà: 5)"
                        className={styles.input}
                        min="1"
                    />
                  </div>
                </>
            )}

            {/* STUDY Ï†ÑÏö© ÌïÑÎìú */}
            {postType === CommunityPostType.STUDY && (
                <>
                  <div className={styles.inputGroup}>
                    <label className={styles.label}>ÏãúÏûëÏùº (ÌïÑÏàò)</label>
                    <input
                        type="date"
                        value={startDate}
                        onChange={(e) => setStartDate(e.target.value)}
                        className={styles.input}
                    />
                  </div>

                  <div className={styles.inputGroup}>
                    <label className={styles.label}>ÏßÑÌñâ ÏöîÏùº/ÏãúÍ∞Ñ (ÏÑ†ÌÉù)</label>
                    <input
                        type="text"
                        value={schedule}
                        onChange={(e) => setSchedule(e.target.value)}
                        placeholder="Ïòà: Îß§Ï£º Ìôî/Î™© Ï†ÄÎÖÅ 7Ïãú"
                        className={styles.input}
                    />
                  </div>
                </>
            )}

            <div className={styles.actions}>
              <button
                  onClick={handleSubmit}
                  disabled={isSubmitting}
                  className={styles.submitButton}
              >
                {isSubmitting ? 'ÏûëÏÑ± Ï§ë...' : 'ÏûëÏÑ± ÏôÑÎ£å'}
              </button>
            </div>
          </div>
        </div>
      </Layout>
  );
}